import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faArrowRight } from '@fortawesome/free-solid-svg-icons'

interface PostNavigatorProps {
  prev: {
    fields: { slug: string }
    frontmatter: { title: string }
  } | null
  next: {
    fields: { slug: string }
    frontmatter: { title: string }
  } | null
}

const NavigatorWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  margin-bottom: 80px;
  gap: 20px;
  max-width: 100%;

  @media (max-width: 768px) {
    flex-direction: column;
  }
`

// [수정] Glassmorphism 스타일 적용
const NavButton = styled(Link)`
  flex: 1;
  display: flex;
  align-items: center;
  padding: 20px;
  text-decoration: none;
  border-radius: 20px; /* 라운드 조금 더 둥글게 */

  /* Glassmorphism Base */
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.1);

  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);

  /* Dark Mode */
  [data-theme='dark'] & {
    background: rgba(30, 30, 40, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
  }

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 40px 0 rgba(31, 38, 135, 0.15);
    background: rgba(255, 255, 255, 0.95);

    [data-theme='dark'] & {
      background: rgba(40, 40, 50, 0.95);
    }

    .title {
      color: var(--primary-color);
    }

    .icon-box {
      background-color: var(--primary-color);
      color: white;
    }
  }
`

const IconBox = styled.div`
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  /* 아이콘 박스 배경도 살짝 투명하게 */
  background: rgba(0, 0, 0, 0.05);
  color: var(--text-sub);

  border-radius: 50%;
  transition: all 0.2s;
  flex-shrink: 0;

  [data-theme='dark'] & {
    background: rgba(255, 255, 255, 0.1);
  }
`

const TextWrapper = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
  padding: 0 15px;
`

const Label = styled.div`
  font-size: 12px;
  color: var(--text-sub);
  margin-bottom: 5px;
  font-weight: 600;
  text-transform: uppercase;
  opacity: 0.8;
`

const Title = styled.div`
  font-size: 16px;
  font-weight: 700;
  color: var(--text-main);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color 0.2s;
`

const PostNavigator: FunctionComponent<PostNavigatorProps> = ({
  prev,
  next,
}) => {
  return (
    <NavigatorWrapper>
      {prev ? (
        <NavButton to={prev.fields.slug}>
          <IconBox className="icon-box">
            <FontAwesomeIcon icon={faArrowLeft} />
          </IconBox>
          <TextWrapper style={{ alignItems: 'flex-start' }}>
            <Label>Previous Post</Label>
            <Title className="title">{prev.frontmatter.title}</Title>
          </TextWrapper>
        </NavButton>
      ) : (
        <div style={{ flex: 1 }} />
      )}

      {next ? (
        <NavButton
          to={next.fields.slug}
          style={{ justifyContent: 'flex-end', textAlign: 'right' }}
        >
          <TextWrapper style={{ alignItems: 'flex-end' }}>
            <Label>Next Post</Label>
            <Title className="title">{next.frontmatter.title}</Title>
          </TextWrapper>
          <IconBox className="icon-box">
            <FontAwesomeIcon icon={faArrowRight} />
          </IconBox>
        </NavButton>
      ) : (
        <div style={{ flex: 1 }} />
      )}
    </NavigatorWrapper>
  )
}

export default PostNavigator
