import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faArrowLeft,
  faCalendarAlt,
  faFolderOpen,
} from '@fortawesome/free-solid-svg-icons'

export type PostHeadInfoProps = {
  title: string
  date: string
  update: string
  category: string
}

// 1. 래퍼 수정 (좌우 여백 제거 -> 꽉 채우기)
const PostHeadInfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: -60px;
  padding: 0;
  padding-bottom: 10px;
  position: relative;
  z-index: 10;

  margin-bottom: 80px;
`

// 2. 카드 수정 (너비 100% + 모서리 조정)
const GlassCard = styled.div`
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.1);

  border-radius: 20px;
  padding: 40px 50px;

  width: 100%;
  max-width: 100%;

  text-align: center;

  [data-theme='dark'] & {
    background: rgba(30, 30, 40, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
  }

  @media (max-width: 768px) {
    padding: 30px 20px;
    border-radius: 15px;
  }
`

// 3. 제목 스타일
const Title = styled.h1`
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--text-main);
  margin-bottom: 20px;
  line-height: 1.3;
  word-break: keep-all;

  @media (max-width: 768px) {
    font-size: 1.8rem;
  }
`

// 4. 메타 정보 (날짜, 카테고리)
const PostData = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  font-size: 1rem;
  color: var(--text-sub);
  font-weight: 500;

  div {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  svg {
    color: var(--primary-color);
  }
`

const Divider = styled.span`
  width: 1px;
  height: 14px;
  background-color: var(--text-sub);
  opacity: 0.5;
`

const PostHeadInfo: FunctionComponent<PostHeadInfoProps> = function ({
  title,
  date,
  update,
  category,
}) {
  const displayDate = update && update !== date ? `${update} (수정됨)` : date

  return (
    <PostHeadInfoWrapper>
      <GlassCard>
        <Title>{title}</Title>
        <PostData>
          <div>
            <FontAwesomeIcon icon={faFolderOpen} />
            {category}
          </div>
          <Divider />
          <div>
            <FontAwesomeIcon icon={faCalendarAlt} />
            {displayDate}
          </div>
        </PostData>
      </GlassCard>
    </PostHeadInfoWrapper>
  )
}

export default PostHeadInfo
