import React, { FunctionComponent } from 'react'
import { graphql } from 'gatsby'
import { PostPageItemType } from 'types/PostItem.types'
import styled from '@emotion/styled'
import Layout from 'components/Layout/Layout'
import PostHead from 'components/Post/PostHead'
import PostContent from 'components/Post/PostContent'
import PostSeries, { SeriesItem } from 'components/Post/PostSeries'
import PostNavigator from 'components/Post/PostNavigator' // import 확인

type PostTemplateProps = {
  data: {
    site: {
      siteMetadata: {
        siteUrl: string
      }
    }
    allMarkdownRemark: {
      edges: PostPageItemType[]
    }
  }
  location: {
    href: string
  }
  pageContext: {
    slug: string
    // 타입 정의 명확화
    prev: {
      fields: { slug: string }
      frontmatter: { title: string }
    } | null
    next: {
      fields: { slug: string }
      frontmatter: { title: string }
    } | null
    series: SeriesItem[]
    lastmod: string
  }
}

const PostWrapper = styled.section`
  position: relative;
  padding: 0;
`

const ContentArea = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 40px;
`

const Post: FunctionComponent<PostTemplateProps> = function ({
  data: {
    site: {
      siteMetadata: { siteUrl },
    },
    allMarkdownRemark: { edges },
  },
  location: { href },
  // pageContext에서 prev, next 구조분해 할당
  pageContext: { slug, series, prev, next },
}) {
  const {
    node: {
      id,
      html,
      tableOfContents,
      frontmatter: {
        title,
        summary,
        date,
        update,
        category,
        thumbnail: {
          childImageSharp: { gatsbyImageData },
          publicURL,
        },
      },
    },
  } = edges[0]

  return (
    <Layout title={title} description={summary} url={href} image={publicURL}>
      <PostWrapper>
        <PostHead
          title={title}
          date={date}
          update={update || date}
          category={category}
          thumbnail={gatsbyImageData}
        />

        <ContentArea>
          {/* 시리즈 */}
          {series && series.length > 0 && <PostSeries series={series} />}

          {/* 본문 */}
          <PostContent
            id={id}
            title={title}
            html={html}
            tableOfContents={tableOfContents}
            siteUrl={siteUrl}
            slug={slug}
          />
        </ContentArea>

        {/* 네비게이터: 오타 수정 및 props 전달 */}
        <PostNavigator prev={prev} next={next} />
      </PostWrapper>
    </Layout>
  )
}

export default Post

export const queryMarkdownDataBySlug = graphql`
  query queryMarkdownDataBySlug($slug: String) {
    site {
      siteMetadata {
        title
        description
        siteUrl
      }
    }
    allMarkdownRemark(filter: { fields: { slug: { eq: $slug } } }) {
      edges {
        node {
          id
          html
          tableOfContents
          frontmatter {
            title
            summary
            date(formatString: "YYYY.MM.DD.")
            update(formatString: "YYYY.MM.DD.")
            category
            thumbnail {
              childImageSharp {
                gatsbyImageData
              }
              publicURL
            }
          }
        }
      }
    }
  }
`
